export default {
	data() {
		return {
			tig: '正在测算中...',
			data: null,
			form: null,
			result: null
		};
	},
	onLoad(options) {
		
		this.form = uni.getStorageSync('form')
		uni.request({
			url: this.$store.state.baseUrl + '/calculation/index/hehun',
			method: 'POST',
			data: options,
			header: {
				"Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
			},
			success: (res) => {
				if (res.data.code == 1) {
					this.data = res.data.data
					this.getResult(this.data.order_sn)
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			}
		})

	},
	methods: {
		getResult(sn) {
			uni.request({
				url: this.$store.state.baseUrl + '/calculation/index/hehun',
				method: 'POST',
				data: {
					order_sn: sn,
					is_free:1,
					"token":uni.getStorageSync('token')
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
				},
				success: (res) => {
					if (res.data.code == 1) {
						setTimeout(() => {
						this.tig = "测算完成，请下滑查看结果"
						this.result=res.data.data
					}, 2000);
					} else {
						this.tig=res.data.msg
					}
				}
			})
		}
	}
}
